.form__container {
  pointer-events: none;
}

.form__border {
  position: absolute;
  width: 50vh;
  height: 52vh;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.5vh solid red;
  border-left: none;
  border-right: none;
  transition-duration: 0.25s;
  transition-property: transform;
  transition-timing-function: ease-out;
  transform: scale(0, 0);
  z-index: 7;
}
.form__top__dot {
  position: absolute;
  top: -1%;
  width: 107%;
  display: flex;
  justify-content: space-between;
}
.form__bottom__dot {
  position: absolute;
  bottom: -1%;
  width: 107%;
  display: flex;
  justify-content: space-between;
}
.dot {
  width: 0.5vh;
  height: 0.5vh;
  background: red;
}

.showing {
  transform: scale(1, 1) !important;
}

.App__header {
  z-index: 1;

  position: absolute;
  width: 50vh;
  height: 50vh;
  background: red;
  display: flex;
  flex-direction: column;
  z-index: 15;
  pointer-events: fill;
  transition-duration: 0.25s;
  transition-property: transform;
  transform: scale(0.5, 0.5);
  transition-timing-function: ease-out;
}
::placeholder {
  color: rgba(240, 240, 240, 0.5);
}
.textfield {
  padding: 0;
  background: rgba(0, 0, 0, 0.25);
  border: 0.25vh solid black;
  margin: 2vh;
  color: whitesmoke;
  outline: none;
  letter-spacing: 0.2ch;
  font-size: 2vh;
}

.text__caption {
  border-right: none;
  border-bottom: none;
  width: 38.75vh;
  height: 4vh;
  padding-left: 0.5vh;
}

.text__content {
  margin-top: 2vh;
  height: 20vh;
  resize: none;
  padding: 1vh;
  border-top: none;
  border-left: none;
}

.post__button {
  padding: 0.9vh;
  border: none;
  width: 25vh;
  height: 5vh;
  background: none;
  align-self: center;
  color: black;
  font-family: "Electrolize", sans-serif;
  letter-spacing: 0.25vh;
  margin-top: 5vh;
  outline: none;
}
.post__background {
  position: absolute;
  top: 37.8vh;
  width: 23.2vh;
  height: 4.7vh;
  background: rgb(255, 90, 90);
  overflow: hidden;
  pointer-events: visibleFill;
}
.post__background:hover {
  background: rgb(255, 154, 154);
}

.jail {
  position: absolute;
  width: 23.2vh;
  height: 4.7vh;
  /* background: yellow; */
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  transform: translateY(-5vh);
  transition-duration: 0.25s;
  transition-property: transform;
  z-index: 5;

  background: rgba(0, 0, 0, 0.5);
}
.jailed {
  transform: translateY(0vh);
}

.line {
  width: 0.5vh;
  height: 5.5vh;
  background: grey;
  transform: rotateZ(25deg);
}
.post__label {
  position: absolute;
  top: 39vh;
  left: 22vh;
  z-index: 5;
  pointer-events: none;
  font-size: 2vh;
}
.mask {
  position: absolute;
  background-color: #fff;
  width: 1.5vh;
  height: 1.5vh;
  z-index: -5;
}
.mask1 {
  top: 37.7vh;
  left: 26.5%;
}
.mask2 {
  top: 37.7vh;
  left: 70.5%;
}
.mask3 {
  top: 41.25vh;
  left: 70.5%;
}
.mask4 {
  top: 41.25vh;
  left: 26.5%;
}
.inputfile {
  width: 0;
  height: 0;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.labelfile {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1.5vh;
  height: 5vh;
  width: 5vh;
  background: red;
}
.labelfile:hover {
  background: rgb(253, 124, 124);
}
.file__Image {
  object-fit: contain;
}

.form__progress {
  position: absolute;
  width: 100%;
  height: 0.25vh;
  background: cyan;

  color: cyan;
  align-self: center;
  margin-top: 100%;
}

.unclickable {
  pointer-events: none;
}
::-webkit-progress-value {
  background: cyan;
}

.image__preview {
  position: absolute;
  width: 50vh;
  height: 20vh;
  bottom: 2.25%;
  background: rgba(0, 0, 0, 0.25);
  transition-duration: 0.25s;
  transform: scale(0, 0);
}
.imagepreview {
  object-fit: contain;
}
/* ERROR */

.error {
  position: absolute;
  left: -11vh;
  top: 3.5%;
  height: 10vh;
  width: 10vh;
  /* background: white; */
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: visibleFill;
  z-index: 7;
}
.error_line {
  position: absolute;
  height: 5%;
  width: 40%;
  animation-name: erroranim;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  box-shadow: 0 0 0.75vh 0.2vh red;
}
.error_dot {
  position: absolute;
  height: 7.5%;
  width: 7.5%;
  border-radius: 100%;
  animation-name: erroranim;
  animation-duration: 0.5s;

  animation-fill-mode: forwards;
  box-shadow: 0 0 0.75vh 0.2vh red;
}
.error_ex {
  position: absolute;
  width: 5%;
  animation-name: erroranim;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  box-shadow: 0 0 0.5vh 0.1vh red;
}

@keyframes erroranim {
  0% {
    background: #ff0000;
  }
  17% {
    background: rgb(255, 177, 177);
  }
  32% {
    background: #ff0000;
  }
  50% {
    background: rgb(255, 177, 177);
  }
  67% {
    background: #ff0000;
  }
  82% {
    background: rgb(255, 177, 177);
  }
  100% {
    background: #ff0000;
  }
}

.error__line3 {
  top: 41%;
  left: 24%;
  transform: rotateZ(-61deg);
  transform-origin: left;
}
.error__line2 {
  top: 41%;
  left: 34%;
  transform: rotateZ(61deg);
  transform-origin: right;
}

.error__dot1 {
  top: 46%;
  left: 17%;
}
.error__dot2 {
  top: 46%;
  left: 74%;
}
.error__dot3 {
  top: -1.5%;
  left: 46.25%;
}

.error__exUp {
  top: 15%;
  height: 20%;
}
.error__exDown {
  top: 40%;
  height: 5%;
  border-radius: 100%;
}

/* CAPTION FULLVIEW */
.caption__fullview {
  position: absolute;
  padding: 0;
  width: 100%;
  height: 17vh;
  background: green;
  top: -42%;
  z-index: 10;
  left: 0;
  display: flex;
  align-items: center;
  justify-items: center;
  justify-content: center;
}

.caption__fullview__caption {
  padding: 0;
  padding-top: 5%;

  width: 100%;
  height: 100%;
  resize: none;
  outline: none;
  text-align: center;
  background: black;
  color: white;
  border: none;
  align-self: center;
  transition-duration: 1s;
  overflow: hidden;
  font-size: 2.25vh;
}
