.content__core {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  user-select: text;
  transition: opacity 0.5s;
}
.fullview {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  transition: opacity 0.5s;
  opacity: 0;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  user-select: none;
}

.fullview__showing {
  opacity: 1 !important;
  z-index: 10;
  pointer-events: visiblefill;
}

.fullview__image__container {
  min-width: 40%;
  min-height: 40%;
  max-height: 85%;
  max-width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.fullview__image {
  object-fit: contain;
}

.contentIndex {
  z-index: 1000;
}

.unhidden {
  opacity: 1;
  pointer-events: fill;
}
.content__core__body {
  width: 80%;
  margin-top: 2%;
  height: 95%;
  font-size: 2vh;

  border-top: 0.1vh solid cyan;
}
.ccb__linear__background {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to right,
    transparent 40%,
    rgba(169, 169, 169, 0.7)
  );
}
.ccb__elements {
  position: relative;
  top: -100%;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
.ccb__elements::-webkit-scrollbar-track {
  margin-bottom: 20vh;
  margin-top: 20vh;
  margin-right: 10vh;
}
.header {
  top: 0;
  width: 100%;
  height: 15%;
  display: grid;
  grid-template-columns: 5fr 2fr;
}
.header__title {
  margin-left: 9%;
  width: 90%;
  height: 100%;
  display: grid;
}
.core__caption {
  position: sticky;
  top: 0;
}
.header__buttons {
  margin-left: 25%;
  height: 100%;
  display: flex;
  font-family: "Electrolize", sans-serif;
  font-size: 1.7vh;
  align-items: center;
}
.header__buttons > div {
  width: 7vh;
  height: 7vh;
  border-radius: 50%;
}
.header__buttons > h1 {
  user-select: none;
}
.core__text {
  font-weight: 400;
  align-self: center;
  color: white;
}

.comment__section {
  bottom: 0;
  margin-top: 5%;
}
.post__comments {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2vh;
}
.core__user_et_name {
  display: flex;
  flex-direction: column;
}
.core__avatar {
  margin-top: 2.5vh;
  align-self: baseline;
}
.individual__comment {
  position: relative;
  width: 80%;
  height: auto;
  background: rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  padding: 0;
  padding-left: 2vh;
  padding-right: 2vh;
  margin: 1.5vh;
  word-wrap: break-word;
}

.core__user_et_comment {
  width: 100%;
  padding: 2vh;
}
.core__user_et_comment > h1 {
  position: relative;
  margin: 0;
  color: white;
  font-weight: 400;
}
.core__user_et_comment > h1:nth-child(2) {
  margin-top: 2vh;
  color: white;
  font-weight: 400;
  width: 90%;
  word-wrap: break-word;
}
.core__comment {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.formcm__design {
  position: fixed;

  bottom: 0;
  left: 50%;
  transform: skewY(20deg) translateX(-50%);
  width: 5vh;
  height: 5vh;
  background: green;
  border-top-left-radius: 100%;
  border-top-right-radius: 100%;
}
.comment__box {
  position: fixed;

  display: flex;
  flex-direction: column;
  bottom: 0;
  left: 49.5%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.777);
  height: 20vh;
  width: 77vh;
  transition: 0.5s;
  z-index: 1000;
}
.comment__input {
  flex: 1;
  outline: none;
  resize: none;
  color: white;
  background: transparent;
  border: none;
  width: 90%;
  text-align: center;
  overflow-x: hidden;
}

.comment__button {
  outline: none;
  height: 20%;
  background: none;
  color: cyan;
  border: none;
}
.comment__button:active {
  background: cyan;
}

.img__button {
  padding: 1.5vh;
  width: 4vh;
  height: 4vh;
  margin-right: 7%;
  fill: cyan;
  background: rgba(0, 255, 255, 0.5);
  border-radius: 50%;
  cursor: pointer;
}
.like__active {
  fill: black;
  background: cyan;
}

.exit__button {
  position: absolute;
  left: 3%;
  bottom: 4%;
  padding: 1vh;
  width: 7vh;
  height: 7vh;
  border-radius: 50%;
  border: 0.3vh solid white;
  fill: white;
  z-index: 1;
}

.core__image__container {
  max-width: 35vh;
  max-height: 20vh;
  background: rgba(0, 0, 0, 0.25);

  margin-left: 10vh;
  margin-right: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.core__image {
  align-self: center;
  justify-self: center;
  object-fit: contain;
}

.core__content {
  margin-left: 10vh;
  margin-right: 5vh;
}

/* BACKGROUND */
.core__entirety {
  position: absolute;
  left: 0;
  top: 0;

  width: 100%;
  height: 100%;

  background: #005780;
  perspective: 777px;
  display: flex;
  flex-direction: column;
  z-index: -10;
}
.core__techno__background {
  position: absolute;
  margin-left: -5vh;
  padding: 0;

  width: 260vh;
  height: 70%;

  background: linear-gradient(#005476, #0071a7, #0071a7, #0071a7, #005476);
  filter: brightness(77%);
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr auto;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr auto;
  overflow: hidden;
  transform: scale(1.5, 1.5) rotateX(-87deg);
}

#core__bgtop {
  left: -14.5%;
  top: -16%;
  padding: 0;
}
#core__bgbottom {
  left: -14.5%;
  padding: 0;
  bottom: -17%;
  transform: scaleY(-1) rotateX(-87deg);
}
.core__fader {
  width: 100%;
  position: absolute;
  height: 100%;
  bottom: 0;
  background: linear-gradient(transparent, #0071a7);
}
.core__box {
  margin: 2vh;
  width: 14vh;
  height: 14vh;
  box-shadow: 0 0 7px 3.5px cyan;
}
