.features__container {
  width: 50vh;
  height: 50vh;
  font-family: "Electrolize", sans-serif;
  z-index: 7;
}
.features {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: lime;
  transform: scale(0, 0);
  transition-duration: 0.25s;
}
.features > h1 {
  font-weight: 300;
  font-size: 5vh;
}

ul {
  padding-left: 11%;
  list-style-type: square;
  overflow-y: hidden;
  pointer-events: Fill;
  font-size: 2.25vh;
}

/* Border */
ul::-webkit-scrollbar-track {
  margin-bottom: 0;
  visibility: hidden;
}
ul:hover::-webkit-scrollbar-track {
  visibility: visible;
}

ul::-webkit-scrollbar-thumb {
  visibility: hidden;
}
ul:hover::-webkit-scrollbar-thumb {
  visibility: visible;
}
.features__border {
  position: absolute;
  width: 50vh;
  height: 52vh;
  top: 23.5%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.5vh solid lime;
  border-left: none;
  border-right: none;
  transition-duration: 0.25s;
  transition-property: transform;
  transition-timing-function: ease-out;
  transform: scale(0, 0);
}
.features__top__dot {
  position: absolute;
  top: -1%;
  width: 107%;
  display: flex;
  justify-content: space-between;
}
.features__bottom__dot {
  position: absolute;
  bottom: -1%;
  width: 107%;
  display: flex;
  justify-content: space-between;
}
.features__dot {
  width: 0.5vh;
  height: 0.5vh;
  background: lime;
}

.features__showing {
  transform: scale(1, 1) !important;
}
