.App {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.App__background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 0;

  /* background: linear-gradient(#005476, #0071a7, #0071a7, #0071a7, #005476); */
  /* filter: brightness(77%); */
}

/* BACKGROUND */
.entirety {
  position: absolute;
  left: 0;
  top: 0;

  width: 100%;
  height: 100%;

  background: #005780;
  perspective: 777px;
  display: flex;
  flex-direction: column;
  z-index: -10;
}
.techno__background {
  position: absolute;
  margin-left: -5vh;
  padding: 0;

  width: 200%;
  height: 70%;

  background: linear-gradient(#005476, #0071a7, #0071a7, #0071a7, #005476);
  filter: brightness(77%);
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr auto;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr auto;
  /* grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr; */
  overflow: hidden;
  transform: translateZ(0) rotateX(-87deg);
  transition: 0.5s;
}
.techanimtop {
  transform: translateZ(200px) rotateX(-87deg) !important;
}
.techanimbottom {
  transform: translateZ(200px) scaleY(-1) rotateX(-87deg) !important;
}

#bgtop {
  left: -14.5%;
  top: -28%;
  padding: 0;
}
#bgbottom {
  left: -14.5%;
  padding: 0;
  bottom: -27%;
  transform: scaleY(-1) rotateX(-87deg);
}
.fader {
  width: 100%;
  position: absolute;
  height: 100%;
  bottom: 0;
  background: linear-gradient(transparent, #0071a7);
}
.box {
  margin: 2vh;
  width: 14vh;
  height: 14vh;
  box-shadow: 0 0 7px 3.5px cyan;
}

/* BACKGROUND */

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.outer {
  z-index: 1;
  transition-duration: 0.5s;
}
.signer {
  z-index: 1;
  transition-duration: 0.5s;
}
.logout {
  margin: 1vh;
  padding: 1vh;
  width: 18vh;
  height: 5vh;
  background: cyan;
  border: none;
  margin-top: 3vh;
  margin-bottom: 3vh;
  font-family: "Electrolize", sans-serif;
  font-weight: 700;
  letter-spacing: 1ch;
  text-align: center;
  font-size: 2vh;
}
.logout:hover {
  background: rgb(169, 255, 255);
}

.inner__signer {
  outline: none;
  width: 17vh;
  height: 5vh;
  margin-left: 1vh;
  margin-right: 1vh;
  background: cyan;
  border: none;
  margin-top: 3vh;
  margin-bottom: 3vh;
  font-family: "Electrolize", sans-serif;
  font-weight: 700;
  letter-spacing: 1ch;
  text-align: center;
}
.inner__signer:hover {
  background: rgb(169, 255, 255);
}

.appvision {
  opacity: 0 !important;
}
